<template>
  <div style="display: flex">
    <div class="intro-text" style="min-height:calc(100vh - 60px);width: 155px;background-color: #ecedf1;flex-shrink: 0;">
      <el-menu :default-openeds="['info']" :collapse="isCollapse" :collapse-transition="false" router background-color="#ecedf1" text-color="#222221"
               active-text-color="#fff" style="border: none" :default-active="$route.path">
        <el-menu-item index="/standard" >
          <i class="el-icon-caret-right"></i>
          <span slot="title"></span>
        </el-menu-item>
        <el-menu-item index="/recordTask"   >
          <i class="el-icon-caret-right"></i>
          <span slot="title"></span>
        </el-menu-item>
        <el-menu-item index="/maintain" >
          <i class="el-icon-caret-right"></i>
          <span slot="title"></span>
        </el-menu-item>

        <el-menu-item index="/problem" >
          <i class="el-icon-caret-right"></i>
          <span slot="title"></span>
        </el-menu-item>
      </el-menu>
      <div style="padding-top: 120px">
        <img src="../../assets/.png" height="234" width="153"/>
      </div>
    </div>

    <div style="padding: 20px 0 0 20px;width: 100%">
      <div>
        <el-input style="width: 300px;margin-right: 10px" placeholder="" v-model="searchText"></el-input>
        <el-button type="primary" @click="load(1)"></el-button>
        <el-button type="info" @click="reset"></el-button>
      </div>

      <el-table :data="tableData" stripe :header-cell-style="{backgroundColor: 'aliceblue', color:'#666'}" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="" prop="id" width="80" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="" prop="equipmentName" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="" prop="department"  align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="" prop="failureLevel" align="center" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="" width="80" prop="description"  align="center" :show-overflow-tooltip="true">
          <template v-slot="scope">
            <el-button plain @click="redirectProblem(scope.row.id,scope.row.equipmentName)" type="danger" plain size="mini"></el-button>
          </template>
        </el-table-column>
        <el-table-column label="" prop="time" align="center" :show-overflow-tooltip="true"></el-table-column>
      </el-table>

      <div style="margin: 10px 0">
        <el-pagination
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-size="pageSize"
            layout="total, prev, pager, next"
            :total="total">
        </el-pagination>
      </div>

      <el-dialog title="" :visible.sync="fromVisible" width="50%"  :close-on-click-modal="false">
        <el-form :model="form" label-width="80px" style="padding-right: 20px" :rules="rules" ref="formRef">
          <el-form-item label="" prop="equipmentName"><el-input v-model="form.equipmentName" placeholder=""></el-input></el-form-item>
          <el-form-item label="" prop="equipmentNum"><el-input v-model="form.equipmentNum" placeholder=""></el-input></el-form-item>
          <el-form-item label="" prop="taskNum"><el-input v-model="form.taskNum" placeholder=""></el-input></el-form-item>
          <div style="display: flex">
            <el-form-item label="" prop="department"><el-input v-model="form.department" placeholder=""></el-input></el-form-item>
            <el-form-item label="" prop="failureLevel"><el-input v-model="form.failureLevel" placeholder=""></el-input></el-form-item>
            <el-form-item label="" prop="taskNum"><el-input v-model="form.taskNum" placeholder=""></el-input></el-form-item>
          </div>
          <el-form-item label="" prop="description">
            <div id="editor"></div>
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="fromVisible = false"> </el-button>
          <el-button type="primary" @click="save"> </el-button>
        </div>
      </el-dialog>


      <el-dialog title="" :visible.sync="fromVisible1" width="50%" >
        <div>
          <div v-html="description"></div>
        </div>
        <div slot="footer" class="dialog-footer">
          <el-button type="primary" @click="fromVisible1 = false"> </el-button>
        </div>
        </el-dialog>
    </div>

  </div>
</template>

<script>
import E from "wangeditor"
export default {
  name: "Problem",
  data() {
    return {
      isCollapse: false,
      tableData: [],  // 
      pageNum: 1,   // 
      pageSize: 10,  // 
      equipmentName: '',
      failureLevel: '',
      taskNum:'',
      searchText: '',
      description: '',
      department: '',
      total: 0,
      fromVisible: false,
      fromVisible1: false,
      form: {},
      user: JSON.parse(localStorage.getItem('honey-user') || '{}'),
      rules: {
        equipmentName: [
          { required: true, message: '', trigger: 'blur' },
        ]
      },
ids:[],
      editor: null,
    }
  },
  created() {
     let nfcNum= this.$route.query.nfcNum;
    console.log(" Now this nfc_num is "+ nfcNum)
    if (nfcNum) {
      //  nfc_num 
      this.loadByNfcNum(nfcNum);
    } else {
      //  equipment_num 
      this.load();
    }
  },
  methods: {
    redirectProblem(a,b) {
      //  $router.push() 
      let encodedEquipmentName = encodeURIComponent(b);
      this.$router.push({
        path: '/problemDetail',
        query: {
          id: a,
          equipmentName: encodedEquipmentName
        }
      });

    },
    loadByNfcNum(nfcNum) {
      //  equipment_num 
      this.$request.get('/problem/selectByNfcNum', {
        params: {
          nfcNum: nfcNum,
          pageNum: this.pageNum,
          pageSize: this.pageSize
        }
      }).then(res => {
        console.log(res);
        this.tableData = res.data.records;
        this.total = res.data.total;
      });
    },
    showContent(description){
      this.description =description
      this.fromVisible1 = true
    },
    closeDialog(){
      this.editor.destroy()
      this.editor = null
    },
    delBatch() {
      if (!this.ids.length) {
        this.$message.warning('')
        return
      }
      this.$confirm('', '', {type: "warning"}).then(response => {
        this.$request.delete('/problem/delete/batch', { data: this.ids }).then(res => {
          if (res.code === '200') {   // 
            this.$message.success('')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 
          }
        })
      }).catch(() => {})
    },
    handleSelectionChange(rows) {   // 
      this.ids = rows.map(v => v.id)
    },
    del(id) {
      this.$confirm('', '', {type: "warning"}).then(response => {
        this.$request.delete('/problem/delete/' + id).then(res => {
          if (res.code === '200') {   // 
            this.$message.success('')
            this.load(1)
          } else {
            this.$message.error(res.msg)  // 
          }
        })
      }).catch(() => {})
    },
    setRichText() {
      this.$nextTick(() => {
        this.editor = new E(`#editor`)
        this.editor.config.uploadImgServer = this.$baseUrl + '/file/editor/upload'
        this.editor.config.uploadFileName = 'file'
        this.editor.config.uploadImgHeaders = {
          token: this.user.token
        }
        this.editor.config.uploadImgParams = {
          type: 'img',
        }
        this.editor.config.uploadVideoServer = this.$baseUrl + '/file/editor/upload'
        this.editor.config.uploadVideoName = 'file'
        this.editor.config.uploadVideoHeaders = {
          token: this.user.token
        }
        this.editor.config.uploadVideoParams = {
          type: 'video',
        }
        this.editor.config.uploadVideoMaxSize = 100000* 1024 * 1024 * 1024 // 1024m
        this.editor.create()  // 
      })
    },
    handleEdit(row) {   // 
      this.form = JSON.parse(JSON.stringify(row))  // form  
      this.fromVisible = true   // 
      this.setRichText()
      this.editor.config.uploadVideoMaxSize = 100000* 1024 * 1024 * 1024 // 1024m
      setTimeout(() => {
        this.editor.txt.html(row.description)  // 
      }, 0)
    },
    handleAdd() {   // 
      this.form = {  }  // 
      this.fromVisible = true   // 
      this.setRichText()
      this.editor.config.uploadVideoMaxSize = 100000* 1024 * 1024 * 1024 // 1024m
    },
    save() {
//  HTML 

      let description = this.editor.txt.html();

//  form  description 
      this.form.description = description;
      //   
      console.log(this.form)
      this.$refs.formRef.validate((valid) => {
        if (valid) {
          this.$request({
            url: this.form.id ? '/problem/update': '/problem/add',
            method: this.form.id ? 'PUT' : 'POST',
            data: this.form,
          }).then(res => {
            if (res.code === '200') {  // 
              this.$message.success('')
              this.load(1)
              this.fromVisible = false
            } else {
              this.$message.error(res.msg)  // 
            }
          })
        }
      })
    },
    reset() {
      this.searchText = '';
      this.load(1);
    },
    load(pageNum) {  // 
      if (pageNum)  this.pageNum = pageNum
      this.$request.get('/problem/selectByPage', {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          equipmentName: this.equipmentName,
          searchText: this.searchText //  searchText 
        }
      }).then(res => {
        this.tableData = res.data.records
        this.total = res.data.total
      })
    },
    handleCurrentChange(pageNum) {
      this.load(pageNum)
    },
  }
}
</script>

<style scoped>

</style>